import React, { useEffect, useState } from "react";
import { RouteComponentProps } from "react-router-dom";
import queryString from "query-string";
import "./App.css";
import { getContent } from "./getContent";

const App: React.FC<RouteComponentProps> = ({ location }) => {
  const query = queryString.parse(location.search);
  const url: string = query.url as string;
  const translate: string = query.translate as string;
  const title: string = query.translate as string;
  console.log(query);
  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit";
    script.async = true;

    const script2 = document.createElement("script");
    script2.type = "text/javascript";
    script2.text = `function googleTranslateElementInit() {
      new google.translate.TranslateElement({pageLanguage: 'en'}, 'google_translate_element');
    }`;

    if (translate) {
      document.body.appendChild(script);
      document.body.appendChild(script2);
    }
    return () => {
      if (translate) {
        document.body.removeChild(script);
        document.body.removeChild(script2);
      }
    };
  }, [translate]);
  return (
    <div className="App">
      <div id="google_translate_element"></div>
      <Transcript url={url} title={title} />
    </div>
  );
};
type ContentProps = {
  url: string;
  title: string;
};

const Transcript: React.FC<ContentProps> = ({ url, title }) => {
  const [content, setContent] = useState("");
  useEffect(() => {
    (async () => {
      const data = await getContent(url);
      if (data) {
        setContent(data);
      }
    })();
  });

  return (
    <div>
      <h1>{title}</h1>
      <div className="content">
        {content.split("\n").map((content: string, index: number) => {
          return <p key={index}>{content}</p>;
        })}
      </div>
    </div>
  );
};
export default App;
